.Container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100wh;
}

.darkmode {
  background-color: black;
  color: white;
  transition: background-color .40s ease-in-out;
}

.lightmode {
  background-color: white;
  color: black;
  transition: background-color .40s ease-in-out;
}

.switch {
  position: relative;
  display: inline-block;
  width: 120px;
  height: 60px;
}

input[type="checkbox"] {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;

  content: "";
  height: 30px;
  top: 15px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked+.slider {
  background-color: #2196F3;
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(87px);
  -ms-transform: translateX(87px);
  transform: translateX(87px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.fa-sun {
  color: #fff;
  position: absolute;
  top: 50%;
  left: 82%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease-in-out;
  visibility: hidden;
}

.fa-moon {
  color: #fff;
  position: absolute;
  top: 50%;
  right: 65%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease-in-out;
  visibility: hidden;
}

.fa-moon.active {
  visibility: visible;
  animation: moveLeft 0.3s ease-in-out;

}

.fa-sun.active {
  visibility: visible;
  animation: moveRight 0.3s ease-in-out;

}